footer.footer {
  margin-top: 40px;
  padding: 40px 0;
  font-size: 87.5%;
  .footer-block {
    > strong {
      display: block;
      padding-bottom: 8px;
    }
  }
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0 0 24px 0;
    li {
      padding: 4px 0;
    }
  }
  .grid {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 16px;
    @media screen and (min-width: 600px) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @media screen and (min-width: 768px) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
      gap: 24px;
    }
  }
}

.prefooter.prefooter {
  display: block;
  height: 88px;
}

.footer_bottom {
  border-bottom: 32px solid var(--color-foreground);
  .footer__copyright {
    text-align: center;
    font-size: 12px;
    margin-bottom: 32px;
  }
}

.footer__payment {
  margin-bottom: 40px;
  ul {
    margin: 0;
    padding: 0;
    text-align: center;
    list-style-type: none;
    li {
      display: inline-block;
      margin: 2px 4px;
    }
  }
}